import 'uni-pages';import '@dcloudio/uni-stat';
import Vue from 'vue'
import App from './App'
import Router from 'uni-simple-router'
import 'common/css/base.css'
import store from './store/index.js'
Vue.config.productionTip = false
import titles from './components/title.vue'
Vue.component('titles', titles)

import uniIcons from "./components/uni-icons/uni-icons.vue"
Vue.component('uniIcons', uniIcons)
import uniPopup from "./components/uni-popup/uni-popup.vue"
Vue.component('uniPopup', uniPopup)

// Vue.prototype.http = http
Vue.prototype.$store = store


Vue.use(Router)
const router = new Router({
	routes: [{
			"path": "/pages/index/index",
			"name": 'index',
			"meta": {
				"title": "文章首页",
				"needToken": false,
			}
		},
		{
			"path": "/pages/login/login",
			"name": 'login',
			"meta": {
				"title": "登录",
				"needToken": false,
			}
		},
		{
			"path": "/pages/info/info",
			"name": 'info',
			"meta": {
				"title": "我的文章",
				"needToken": true,
			}
		},
		{
			"path": "/pages/info/visitor/visitor",
			"name": 'visitor',
			"meta": {
				"title": "访客追踪",
				"needToken": true,
			}
		},
		{
			"path": "/pages/article/reprint/reprint",
			"name": 'reprint',
			"meta": {
				"title": "转载首页",
				"needToken": true,
			}
		},
		{
			"path": "/pages/article/original/original",
			"name": 'original',
			"meta": {
				"title": "原创首页",
				"needToken": true,
			}
		},
		{
			"path": "/pages/article/reprint/tips/tips",
			"name": 'tips',
			"meta": {
				"title": "转载提示",
				"needToken": true,
			}
		},
		{
			"path": "/pages/user/user",
			"name": 'user',
			"meta": {
				"title": "个人中心",
				"needToken": true,
			}
		},
		{
			"path": "/pages/article/original/edit/edit",
			"name": 'edit',
			"meta": {
				"title": "创建文章",
				"needToken": true,
			}
		},
		{
			"path": "/pages/article/original/addDisplayBit/addDisplayBit",
			"name": 'addDisplayBit',
			"meta": {
				"title": "原创添加额外展示位",
				"needToken": true,
			}
		},
		{
			"path": "/pages/article/userinfo/userinfo",
			"name": 'userinfo',
			"meta": {
				"title": "形象展示位",
				"needToken": true,
			}
		},
		{
			"path": "/pages/article/productInfo/productInfo",
			"name": 'productInfo',
			"meta": {
				"title": "添加产品/案例/服务",
				"needToken": true,
			}
		},
		{
			"path": "/pages/article/consultation/consultation",
			"name": 'consultation',
			"meta": {
				"title": "快速咨询",
				"needToken": true,
			}
		},
		{
			"path": "/pages/article/reprint/edits/edits",
			"name": 'edits',
			"meta": {
				"title": "转载编辑文章",
				"needToken": true,
			}
		},
		{
			"path": "/pages/article/reprint/editingArticles/editingArticles",
			"name": 'editingArticles',
			"meta": {
				"title": "转载编辑文章",
				"needToken": true,
			}
		},
		{
			"path": "/pages/article/comment/comment",
			"name": 'comment',
			"meta": {
				"title": "发布评论",
				"needToken": true,
			}
		},
		{
			"path": "/pages/start/start",
			"name": 'start',
			"meta": {
				"title": "跳转页",
				"needToken": false,
			}
		},
	]
})

const turnTo = (to, next) => {

	if (to.meta.needToken && !store.state.user.token) {
		console.log(to);
		console.log(next)
		uni.setStorageSync('nextURL',to)
		next({
			name: 'login'
		})
	} else {
		console.log(2)
		next()
	}
}

router.beforeEach((to, from, next) => {
	//todo 测试，暂时全部注释
	// next()
	if(store.state.user.hasLogin){
		next()
	}else{
		turnTo(to, next);
	}
	
})





App.mpType = 'app'
const app = new Vue({
	...App
})
app.$mount()